/* General page setup */
html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: 'Raleway', sans-serif;
    background-color: #D6C0B3;  /* Match the background color of the registration page */
    color: black;
    width: 100%;
}

/* Main container for the Add Recipe page */
.add-recipe-page {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
    background-color: #DFD3C3;
    min-height: 100vh;
    box-sizing: border-box;
}

/* Page title styling */
.add-recipe-page h2 {
    font-size: 28px;
    color: #d77a65;
    font-family: 'EB Garamond', serif;
    font-weight: bold;
    margin-bottom: 20px;
}

/* Form container */
form {
    width: 100%;
    max-width: 600px;
    background-color: #FFFFFF;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Form group styling */
.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    font-size: 16px;
    color: #6f6666;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}

/* Input fields styling */
.form-group input[type="text"],
.form-group input[type="file"],
textarea {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 16px;
    box-sizing: border-box;
    margin-bottom: 10px;
}

/* Ingredient group styling */
.ingredient-group {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
}

.ingredient-group input[type="text"] {
    flex: 1;
}

/* Add button for ingredients and instructions */
button[type="button"] {
    background-color: #c5705d;
    color: #FFFFFF;
    border: none;
    border-radius: 5px;
    padding: 8px 12px;
    font-size: 14px;
    cursor: pointer;
    margin-top: 5px;
}

button[type="button"]:hover {
    background-color: #ab886d;
}

/* Instruction textarea styling */
.instruction-group {
    margin-bottom: 10px;
}

.instruction-group textarea {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 16px;
    resize: vertical;
}

/* Submit button */
.submit-button {
    width: 100%;
    padding: 12px;
    background-color: #c5705d;
    color: #FFFFFF;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 20px;
}

.submit-button:hover {
    background-color: #ab886d;
}

/* Success and error messages */
.success,
.error {
    margin-top: 15px;
    padding: 10px;
    border-radius: 5px;
    font-size: 14px;
    width: 100%;
    text-align: center;
}

.success {
    color: #0F5132;
    background-color: #D1E7DD;
    border: 1px solid #BADBCC;
}

.error {
    color: #842029;
    background-color: #F8D7DA;
    border: 1px solid #F5C2C7;
}

/* Animation for success/error messages */
@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.success, .error {
    animation: fadeIn 0.3s ease-in-out;
}
